<template>
  <div id = "app">
    <el-container>
      <el-header style="font-size: 40px; color: #FFFFFF ;background-color: #0665d0"
        >环境温湿度检测系统</el-header>
      <el-container>
        <el-aside width="230px" height = 100% >
          <h1>管理员</h1>
          <el-menu
          router="true"
            default-active="/admin"
            class="el-menu-vertical-demo"
            height = 100%
          >
            <el-menu-item index="/admin">
              <i class="el-icon-s-custom"></i>
              <span slot="title">员工管理</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

                <el-main style="background-color:#F2F6FC">
                    <!-- 表单 -->
                    <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                        <el-form-item label="姓名">
                            <el-input v-model="searchForm.name" placeholder="姓名"></el-input>
                        </el-form-item>

                        <el-form-item label="性别">
                            <el-select v-model="searchForm.gender" placeholder="性别">
                                <el-option label="男" value="1"></el-option>
                                <el-option label="女" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        
                        
                        <el-form-item>
                            <el-button type="primary" @click="onSubmit">查询</el-button>
                        </el-form-item>
                        <el-button type="primary" @click="onSava">新增</el-button>
                    </el-form>

                    <!-- 表格 -->
                    <el-table :data="tableData" border>
                        <el-table-column prop="id" label="ID" width="180"></el-table-column>
                        <el-table-column prop="name" label="姓名" width="180"></el-table-column>
                        <el-table-column label="图像" width="180">
                            <template slot-scope="scope">
                                <img :src="scope.row.image" width="100px" height="70px">
                            </template>
                        </el-table-column>
                        <el-table-column label="性别" width="140">
                            <template slot-scope="scope">
                                {{scope.row.gender == 1 ? '男':'女'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="job" label="职位" width="140"></el-table-column>
                        <el-table-column prop="entrydate" label="入职日期" width="180"></el-table-column>
                        <el-table-column prop="updateTime" label="最后操作时间" width="230"></el-table-column>
                        <el-table-column label="操作" >
                             <template slot-scope="scope">
                            <el-button type="primary" size="mini" >编辑</el-button>
                            <el-button type="danger" size="mini" @click="danger(scope.row.id)">删除</el-button>
                             </template>
                        </el-table-column>
                              
                    </el-table>
                <el-dialog
                            :visible.sync="savaDialogVisible"
                            title="新增员工数据"
                            @close="handleCloseSavaDialog"
                        >
                            <el-form ref="savaForm" :model="savaForm" label-width="100px">
                                <el-form-item label="用户名">
                                    <el-input v-model="savaForm.username"></el-input>
                                </el-form-item>
                                <el-form-item label="姓名">
                                    <el-input v-model="savaForm.name"></el-input>
                                </el-form-item>
                                <!-- <el-form-item label="图像">
                                    <el-upload
                                        class="avatar-uploader"
                                        action="api/upload"
                                        :show-file-list="false"
                                        :on-success="handleAvatarSuccess"
                                        :before-upload="beforeAvatarUpload"
                                        :headers="{
                                            token: 'admin'
                                    }">
                                        <img v-if="image" :src="image" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                        </el-upload>
                                </el-form-item> -->
                            <el-form-item label="性别">
                                <el-select v-model="savaForm.gender" placeholder="性别">
                                    <el-option label="男" value="1"></el-option>
                                    <el-option label="女" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="职位">
                                <el-input v-model="savaForm.job"></el-input>
                            </el-form-item>
                                <el-button type="primary" @click="saveData">保存</el-button>
                            </el-form>
                        </el-dialog>
                    <br>
                    
                    <!-- 分页条 -->
                    <!-- Pagination 分页 -->
                    <el-pagination background layout="total,sizes, prev, pager, next, jumper" 
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :total="pagination.total"></el-pagination>

                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import axios from 'axios';
import { getToken } from '@/utils/auth'
export default {
    data() {
        return {
            tableData: [],
            searchForm: {
               name:"",
               gender:""
            },
            pagination: {
                page: 1, // 当前页码
                pageSize: 10, // 每页大小
                total: 10 // 总条数
            },
            savaDialogVisible: false,
            savaForm: {
                username: "",
                name: "",
                image: "",
                gender: "",
                job: ""
            }
        }
    },
    created(){
        this.queryAllData();
    },
    methods: {
    //     beforeAvatarUpload(file) {
    //     const isJPG = file.type === 'image/jpeg';
    //     const isLt2M = file.size / 1024 / 1024 < 2;

    //     if (!isJPG) {
    //       this.$message.error('上传头像图片只能是 JPG 格式!');
    //     }
    //     if (!isLt2M) {
    //       this.$message.error('上传头像图片大小不能超过 2MB!');
    //     }
    //     return isJPG && isLt2M;
    //   },
    //     handleAvatarSuccess(res, file) {
    //     this.savaForm.image = URL.createObjectURL(res.data);
    //   },
        saveData() {
        // 发送 POST 请求保存数据
        axios
            .post("/api/emps", this.savaForm, {
            headers: {
                token: "admin",
            },
            })
            .then((response) => {
            // 保存成功，关闭新增对话框
            this.savaDialogVisible = false;
            // 刷新数据列表
            this.updateTable();
            })
            .catch((error) => {
            console.error("保存数据失败：", error);
            });
        },
        onSava() {
        this.savaDialogVisible = true;
        },
        onSubmit:function(){
            this.updateTable();
        },
        handleSizeChange:function(val){
            this.pagination.pageSize = val ;
            this.updateTable();
        },
        handleCurrentChange:function(val){
            this.pagination.page = val;
            this.updateTable();
        },
        queryAllData(){
            axios.get("/api/emps",{
                headers:{'token':'admin'}}
                ).then((result) => {
                this.pagination.total = result.data.data.total;
                this.tableData = result.data.data.rows;  
        }).catch((error) => {
                    console.error('请求失败:', error);
            });
        },
        updateTable(){
            let requestData = {
                name:this.searchForm.name,
                gender: this.searchForm.gender,
                page: this.pagination.page, // 当前页码
                pageSize: this.pagination.pageSize, // 每页大小
                total: this.pagination.total // 总条数
            }
            axios.get("/api/emps",{
                headers:{'token':getToken()}, 
                params: requestData }
            ).then((result) => {
                this.pagination.total = result.data.data.total;
                this.tableData = result.data.data.rows;  
        }).catch((error) => {
                    console.error('请求失败:', error);
            });
        },
        danger(id){
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            axios.delete(`/api/emps/${id}`,
                {headers:{'token':'admin'}}
            )
            .then(() =>{
                this.$message({
                        type: 'success',
                        message: '删除成功!'
                        })
            }).catch(error => {
            // 删除失败
            console.error('删除失败：', error);
                 });})
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
        }
    },
    mounted () {

    }
}
</script>

<style>
html,body,#app,.el-container{
        /*设置内部填充为0，几个布局元素之间没有间距*/
        padding: 0px;
         /*外部间距也是如此设置*/
        margin: 0px;
        /*统一设置高度为100%*/
        height: 100%;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;}

</style>