<template>
  <div id = "app">
    <el-container>
      <el-header style="font-family:Inter,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,Liberation Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;font-size: 40px; color: #FFFFFF ;background-color: #0665d0"
        >环境温湿度检测系统</el-header>
      <el-container>
        <el-aside width="230px" height = 100% >
          <h1>系统管理</h1>
          <el-menu
          router="true"
            default-active="/device"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            height = 100%
          >
            <el-menu-item index="/device" >
              <i class="el-icon-cpu"></i>
              <span>设备管理</span>
            </el-menu-item>
            <!-- <el-menu-item index="/emp">
              <i class="el-icon-s-custom"></i>
              <span>员工管理</span>
            </el-menu-item> -->
            <el-menu-item index="/sensor">
              <i class="el-icon-odometer"></i>
              <span>温湿度数据</span>
            </el-menu-item>
            <el-menu-item index="/dashborad">
              <i class="el-icon-monitor"></i>
              <span>数据可视化</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

        <el-main style="background-color:#F2F6FC">
        <div>
          <!-- 表单 -->
          <el-form :inline="true" :model="searchForm" class="demo-form-inline">
            <el-form-item label="id">
              <el-input v-model="searchForm.id" placeholder="ID"></el-input>
            </el-form-item>

            <el-form-item label="状态">
              <el-select v-model="searchForm.status" placeholder="状态">
                <el-option label="启用" value="1"></el-option>
                <el-option label="停用" value="0"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
            <el-button type="primary" @click="onSava">新增</el-button>
          </el-form>
        </div>
          <!-- 表格 -->
          <el-table :data="tableData" border>
            <el-table-column
              prop="id"
              label="  ID"
              width="80"
            ></el-table-column>
            <el-table-column
              prop="ip"
              label="  IP地址"
              width="180"
            ></el-table-column>
            <el-table-column
              prop="port"
              label="  端口号"
              width="80"
            ></el-table-column>
            <el-table-column
              prop="model"
              label="  设备型号"
              width="180"
            ></el-table-column>

            <el-table-column label="状态" width="140">
              <template slot-scope="scope">
                {{ scope.row.status == 1 ? "启用" : "停用" }}
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="primary" size="mini" @click="edit(scope.row)"
                  >编辑</el-button
                >
                <el-button type="danger" size="mini">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        <el-dialog
            :visible.sync="savaDialogVisible"
            title="新增设备数据"
            @close="handleCloseSavaDialog"
        >
            <el-form ref="savaForm" :model="savaForm" label-width="100px">
                <el-form-item label="IP地址">
                    <el-input v-model="savaForm.ip"></el-input>
                </el-form-item>
                <el-form-item label="端口号">
                    <el-input v-model="savaForm.port"></el-input>
                </el-form-item>
                <el-form-item label="设备型号">
                    <el-input v-model="savaForm.model"></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select v-model="savaForm.status" placeholder="状态">
                        <el-option label="启用" value="1"></el-option>
                        <el-option label="停用" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-button type="primary" @click="saveData">保存</el-button>
            </el-form>
        </el-dialog>
          <!-- 编辑对话框 -->
          <el-dialog
            :visible.sync="editDialogVisible"
            title="编辑设备数据"
            @close="handleCloseEditDialog"
          >
            <el-form ref="editForm" :model="editForm" label-width="100px">
              <el-form-item label="ID">
                <el-input v-model="editForm.id" disabled></el-input
              ></el-form-item>
              <el-form-item label="IP地址">
                <el-input v-model="editForm.ip"></el-input>
              </el-form-item>
              <el-form-item label="端口号">
                <el-input v-model="editForm.port"></el-input>
              </el-form-item>
              <el-form-item label="设备型号">
                <el-input v-model="editForm.model"></el-input>
              </el-form-item>
              <el-form-item label="状态">
                <el-select v-model="editForm.status" placeholder="状态">
                  <el-option label="启用" value="1"></el-option>
                  <el-option label="停用" value="0"></el-option>
                </el-select>
              </el-form-item>
              <el-button type="primary" @click="saveEdit">保存</el-button>
            </el-form>
          </el-dialog>
          <br />

          <!-- 分页条 -->
          <!-- Pagination 分页 -->
          <el-pagination
            background
            layout="total,sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="pagination.total"
          ></el-pagination>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import { getToken } from "@/utils/auth";
export default {
  data() {
    return {
      tableData: [],
      searchForm: {
        id: "",
        status: "",
      },
      pagination: {
        page: 1, // 当前页码
        pageSize: 10, // 每页大小
        total: 10, // 总条数
      },
      editDialogVisible: false, // 编辑对话框是否可见
      editForm: {
        id: "",
        ip: "",
        port: "",
        model: "",
        status: "",
      }, // 编辑表单数据
        savaDialogVisible: false, // 新增对话框是否可见
        savaForm: {
            ip: "",
            port: "",
            model: "",
            status: "",
        }, // 新增表单数据
    };
  },
  created() {
    this.queryAllData();
  },
  methods: {
    onSubmit: function () {
      this.updateTable();
    },
    handleSizeChange: function (val) {
      this.pagination.pageSize = val;
      this.updateTable();
    },
    handleCurrentChange: function (val) {
      this.pagination.page = val;
      this.updateTable();
    },
    queryAllData() {
      axios
        .get("/api/device", {
          headers: { token: getToken() },
        })
        .then((result) => {
          this.pagination.total = result.data.data.total;
          this.tableData = result.data.data.rows;
        })
        .catch((error) => {
          console.error("请求失败:", error);
        });
    },
    updateTable() {
      let requestData = {
        id: this.searchForm.id,
        status: this.searchForm.status,
        page: this.pagination.page, // 当前页码
        pageSize: this.pagination.pageSize, // 每页大小
        total: this.pagination.total, // 总条数
      };
      axios
        .get("/api/device", {
          headers: {
            token: getToken(),
          },
          params: requestData,
        })
        .then((result) => {
          this.pagination.total = result.data.data.total;
          this.tableData = result.data.data.rows;
        })
        .catch((error) => {
          console.error("请求失败:", error);
        });
    },
    edit(row) {
      // 将行数据填充到编辑表单中
      this.editForm = { ...row };

      // 打开编辑对话框
      this.editDialogVisible = true;
    },
    onSava() {
      this.savaDialogVisible = true;
    },
    saveEdit() {
      // 发送 PUT 请求更新数据
      axios
        .put("/api/device/update", this.editForm, {
          headers: {
            token: getToken(),
          },
        })
        .then((response) => {
          // 更新成功，关闭编辑对话框
          this.editDialogVisible = false;
          // 刷新数据列表
          this.updateTable();
        })
        .catch((error) => {
          console.error("更新数据失败：", error);
        });
    },
    saveData() {
      // 发送 POST 请求保存数据
      axios
        .post("/api/device", this.savaForm, {
          headers: {
            token: getToken(),
          },
        })
        .then((response) => {
          // 保存成功，关闭新增对话框
          this.savaDialogVisible = false;
          // 刷新数据列表
          this.updateTable();
        })
        .catch((error) => {
          console.error("保存数据失败：", error);
        });
    },
    handleCloseEditDialog() {
      // 关闭编辑对话框时清空表单数据
      this.editForm = {};
    },
    handleCloseSavaDialog() {
      // 关闭新增对话框时清空表单数据
      this.savaForm = {};
    },
  },
  mounted() {},
};
</script>

<style>
.el-form-item .el-select {
  width: 100%;
}
html,body,#app,.el-container{
        /*设置内部填充为0，几个布局元素之间没有间距*/
        padding: 0px;
         /*外部间距也是如此设置*/
        margin: 0px;
        /*统一设置高度为100%*/
        height: 100%;
}
</style>