<template>
  <div id = "app">
    <el-container>
      <el-header style="font-family:Inter,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,Liberation Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;font-size: 40px; color: #FFFFFF ;background-color: #0665d0"
        >环境温湿度检测系统</el-header>
      <el-container>
        <el-aside width="230px" height = 100% >
          <h1>系统管理</h1>
          <el-menu
          router="true"
            default-active="/dashborad"
            class="el-menu-vertical-demo"
            height = 100%
          >
            <el-menu-item index="/device" >
              <i class="el-icon-cpu"></i>
              <span slot="title">设备管理</span>
            </el-menu-item>
            <!-- <el-menu-item index="/emp">
              <i class="el-icon-s-custom"></i>
              <span slot="title">员工管理</span>
            </el-menu-item> -->
            <el-menu-item index="/sensor">
              <i class="el-icon-odometer"></i>
              <span slot="title">温湿度数据</span>
            </el-menu-item>
            <el-menu-item index="/dashborad">
              <i class="el-icon-monitor"></i>
              <span slot="title">数据可视化</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

        <el-main height = 100% style="background-color:#F2F6FC">
          <div class="dashboard">
            <el-row>
                <el-col :span="24" >
                <div ref="lineChart" class="chart-container"></div>
                </el-col>
              <el-col :span="12" >
                <div class="chart-container" >
                  <h3 >温度统计</h3>
                  <div ref="temperaturePieChart" style="height: 200px "></div>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="chart-container">
                  <h3>湿度统计</h3>
                  <div ref="humidityPieChart" style="height: 200px"></div>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import * as echarts from "echarts";
import axios from "axios";
import { getToken } from "@/utils/auth";
export default {
  data() {
    return {
      sensorData: [], // 从后端获取的传感器数据
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      axios
        .get("/api/sensor/dashboard", {
          headers: { token: getToken() },
        })
        .then((response) => {
          this.sensorData = response.data.data;
          this.renderLineChart();
          this.renderTemperaturePieChart();
          this.renderHumidityPieChart();
        })
        .catch((error) => {
          console.error("获取传感器数据失败：", error);
        });
    },
    renderTemperaturePieChart() {
      const temperaturePieChart = echarts.init(this.$refs.temperaturePieChart);
      const count = {
        "<0": 0,
        "0-25": 0,
        ">25": 0,
      };
      this.sensorData.forEach((data) => {
        if (data.temperature < 0) {
          count["<0"]++;
        } else if (data.temperature <= 25) {
          count["0-25"]++;
        } else {
          count[">25"]++;
        }
      });
      // 配置扇形统计图的选项
      const options = {
        // 配置扇形统计图的其他选项
        series: [
          {
            type: "pie",
            data: [
              { value: count["<0"], name: "0度以下" },
              { value: count["0-25"], name: "0-25度" },
              { value: count[">25"], name: "25度以上" },
            ],
            // 设置饼图的样式
            label: {
              show: true,
              formatter: "{b}: {c} ({d}%)",
            },
          },
        ],
        // color: ['#37a2da', '#32c5e9', '#9fe6b8'] // 设置饼图的颜色
      };

      // 使用配置项渲染扇形统计图
      temperaturePieChart.setOption(options);
    },
    renderHumidityPieChart() {
      const humidityPieChart = echarts.init(this.$refs.humidityPieChart);
      const count = {
        "0-20": 0,
        "20-40": 0,
        "40-60": 0,
        "60以上": 0,
      };
      this.sensorData.forEach((data) => {
        if (data.humidity >= 0 && data.humidity < 20) {
          count["0-20"]++;
        } else if (data.humidity >= 20 && data.humidity < 40) {
          count["20-40"]++;
        } else if (data.humidity >= 40 && data.humidity < 60) {
          count["40-60"]++;
        } else {
          count["60以上"]++;
        }
      });
      // 配置扇形统计图的选项
      const options = {
        // 配置扇形统计图的其他选项
        series: [
          {
            type: "pie",
            data: [
              { value: count["0-20"], name: "0-20%" },
              { value: count["20-40"], name: "20%-40%" },
              { value: count["40-60"], name: "40%-60%" },
              { value: count["60以上"], name: "60%以上" },
            ],
          },
        ],
      };
      // 使用配置项渲染扇形统计图
      humidityPieChart.setOption(options);
    },
    renderLineChart() {
      const chartDom = this.$refs.lineChart;
      const myChart = echarts.init(chartDom);
      const option = {
        title: {
          text: "温湿度变化统计",
        },
        tooltip: {
          trigger: "axis",
        },
        xAxis: {
          type: "category",
          data: this.sensorData.map((item, index) => index + 1),
        },
        yAxis: [
          {
            type: "value",
            name: "温度",
            axisLabel: {
              formatter: "{value} °C",
            },
          },
          {
            type: "value",
            name: "湿度",
            axisLabel: {
              formatter: "{value} %",
            },
          },
        ],
        series: [
          {
            name: "温度",
            type: "line",
            data: this.sensorData.map((item) => item.temperature),
          },
          {
            name: "湿度",
            type: "line",
            yAxisIndex: 1,
            data: this.sensorData.map((item) => item.humidity),
          },
        ],
      };

      myChart.setOption(option);
    },
  },
};
</script>

<style scoped>
.chart-container {
  width: 100%;
  height: 400px;
  
}
html,body,#app,.el-container{
        /*设置内部填充为0，几个布局元素之间没有间距*/
        padding: 0px;
         /*外部间距也是如此设置*/
        margin: 0px;
        /*统一设置高度为100%*/
        height: 100%;
}
</style>