import Vue from 'vue'
import Router  from 'vue-router'
import LoginView from '@/views/LoginView'
import DeviceView from '@/views/DeviceView'
import SensorView from '@/views/SensorView'
import DashBoardView from '@/views/DashBoardView'
import AdminView from '@/views/AdminView'
import NotFoundView from '@/views/NotFoundView'
Vue.use(Router)
const router = new Router({
    
//   mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/login' // 默认重定向到登录页面
    },
    {
      path: '/login',
      name: 'login',
      component: LoginView
    },
    {
      path: '/device',
      name: 'deivice',
      component: DeviceView,
      meta: { requiresAuth: true } // 添加一个meta字段表示需要登录才能访问的页面
    }, 
    {
      path: '/sensor',
      name: 'sensor',
      component: SensorView,
      meta: { requiresAuth:true } // 添加一个meta字段表示需要登录才能访问的页面
    },
    {
      path: '/dashborad',
      name: 'dashboard',
      component: DashBoardView,
      meta: { requiresAuth:true } // 添加一个meta字段表示需要登录才能访问的页面
    },
    {
      path: '/admin',
      name: 'admin',
      component: AdminView,
      meta: { requiresAuth:true ,role:'admin'} // 添加一个meta字段表示需要登录才能访问的页面

    },
    {
      path: '/404',
      name: 'notfound',
      component: NotFoundView,
    },
      // 404 page must be placed at the end !!!
    { 
        path: '*', 
        name: 'notfound',
        redirect: '/404',
        component: NotFoundView
    }
  ]
});
// 全局前置守卫
router.beforeEach((to, from, next) => {
  // 检查路由元信息中是否设置了requiresAuth，如果设置了则需要验证登录状态

  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 检查用户是否已登录，这里假设有一个名为isAuthenticated的方法用于检查登录状态
    const role = localStorage.getItem("username");
    if (!role) {
      // 如果用户未登录，则重定向到登录页面
      next({ path: '/login', query: { redirect: to.fullPath }});
    } else {
        if (to.meta.role && role !== to.meta.role) {
        // 如果用户角色不符合要求，重定向到登录页面
        next('/login')
      } else {
        // 用户已登录且角色符合要求，允许进入目标页面
        next()
      }
    }
  } else {
    // 如果路由元信息中未设置requiresAuth，则直接导航
    next();
  }
});

export default router
