<template>
  <div id = "app">
    <el-container>
      <el-header style="font-family:Inter,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,Liberation Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;font-size: 40px; color: #FFFFFF ;background-color: #0665d0"
        >环境温湿度检测系统</el-header>
      <el-container>
        <el-aside width="230px" height = 100% >
          <h1>系统管理</h1>
          <el-menu
          router="true"
            default-active="/sensor"
            class="el-menu-vertical-demo"
            height = 100%
          >
            <el-menu-item index="/device" >
              <i class="el-icon-cpu"></i>
              <span slot="title">设备管理</span>
            </el-menu-item>
            <!-- <el-menu-item index="/emp">
              <i class="el-icon-s-custom"></i>
              <span slot="title">员工管理</span>
            </el-menu-item> -->
            <el-menu-item index="/sensor">
              <i class="el-icon-odometer"></i>
              <span slot="title">温湿度数据</span>
            </el-menu-item>
            <el-menu-item index="/dashborad">
              <i class="el-icon-monitor"></i>
              <span slot="title">数据可视化</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

        <el-main style="background-color:#F2F6FC">
          <!-- 表单 -->
          <el-form :inline="true" :model="searchForm" class="demo-form-inline">
            <el-form-item label="设备ID">
              <el-input
                v-model="searchForm.deviceId"
                placeholder="设备ID"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
          </el-form>

          <!-- 表格 -->
          <el-table :data="tableData" border>
            <el-table-column
              prop="id"
              label="  ID"
              width="80"
            ></el-table-column>
            <el-table-column
              prop="deviceId"
              label="  设备ID"
              width="180"
            ></el-table-column>
            <el-table-column
              prop="temperature"
              label="  温度"
              width="80"
            ></el-table-column>
            <el-table-column
              prop="humidity"
              label="  湿度"
              width="180"
            ></el-table-column>
            <el-table-column
              prop="createTime"
              label="时间"
              width="180"
            ></el-table-column>

            <el-table-column label="操作">
              <el-button type="primary" size="mini">编辑</el-button>
              <el-button type="danger" size="mini">删除</el-button>
            </el-table-column>
          </el-table>

          <br />

          <!-- 分页条 -->
          <!-- Pagination 分页 -->
          <el-pagination
            background
            layout="total,sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="pagination.total"
          >
          </el-pagination>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import { getToken } from "@/utils/auth";
export default {
  data() {
    return {
      tableData: [],
      searchForm: {
        deviceId: "",
      },
      pagination: {
        page: 1, // 当前页码
        pageSize: 10, // 每页大小
        total: 10, // 总条数
      },
    };
  },
  created() {
    this.queryAllData();
  },
  methods: {
    updateTable() {
      let requestData = {
        deviceId: this.searchForm.deviceId,
        page: this.pagination.page, // 当前页码
        pageSize: this.pagination.pageSize, // 每页大小
      };
      axios
        .get("/api/sensor", {
          headers: { token: getToken() },
          params: requestData,
        })
        .then((response) => {
          this.pagination.total = response.data.data.total;
          this.tableData = response.data.data.rows;
        })
        .catch((error) => {
          console.error("查询失败：", error);
        });
    },
    onSubmit: function () {
      this.updateTable();
    },
    handleSizeChange: function (val) {
      this.pagination.pageSize = val;
      this.updateTable();
    },
    handleCurrentChange: function (val) {
      this.pagination.page = val;
      this.updateTable();
    },
    queryAllData() {
      axios
        .get("/api/sensor", {
          headers: { token: getToken() },
        })
        .then((result) => {
          this.pagination.total = result.data.data.total;
          this.tableData = result.data.data.rows;
        })
        .catch((error) => {
          console.error("请求失败:", error);
        });
    },
  },
  mounted() {},
};
</script>

<style>
html,body,#app,.el-container{
        /*设置内部填充为0，几个布局元素之间没有间距*/
        padding: 0px;
         /*外部间距也是如此设置*/
        margin: 0px;
        /*统一设置高度为100%*/
        height: 100%;
}
</style>