<template>
  <div class="login-container">
    <el-card class="login-card" shadow="hover">
      <h3 slot="header" class="text-center">用户登录</h3>
      <el-form ref="loginForm" :model="loginForm" :rules="loginFormRules" label-width="80px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="loginForm.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="loginForm.password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>

import axios from 'axios';
import { MessageBox } from 'element-ui'; // 引入Element UI的消息框组件
import { setToken } from '@/utils/auth'
import { setUsername } from '@/utils/auth'
export default {
  data() {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm() {
        if(this.loginForm.username === '' || this.loginForm.password === '') {
          MessageBox.alert('用户名或密码不能为空', '提示', {
            confirmButtonText: '确定'
          });
          return;
        }
        if(this.loginForm.username == "admin" && this.loginForm.password == "admin") {
          setToken('admin');
          setUsername('admin');
          MessageBox.alert('登录成功', '提示', {
            confirmButtonText: '确定',
            callback: () => {
            this.$router.replace('/admin');
        }});}
        else{
      this.$refs.loginForm.validate(valid => {
        if (valid) {
// 构造请求路径
          const url = `/api/login`;
          
          // 发送登录请求
          axios.post(url, this.loginForm)
            .then(response=> {
              if(response.data.code !== 1) {
                MessageBox.alert('账号或密码错误', '登陆失败', {
                  confirmButtonText: '确定'
                });
                return;
              }
              setToken(response.data.data);
              setUsername(this.loginForm.username);
              MessageBox.alert('登录成功', '提示', {
                confirmButtonText: '确定',
                callback: () => {
                this.$router.replace('/dashborad');
            }});
            })
            .catch(error => {
              console.error('登录失败:', error);
              MessageBox.alert('服务器错误', '提示', {
                  confirmButtonText: '确定'
                });
            });
        } else {
          console.log('表单验证失败');
          return false;
        }
      });
        }

    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-card {
  width: 400px;
}

.text-center {
  text-align: center;
}
</style>
